.user-background-image {
  position: absolute;
  background: linear-gradient(
      to bottom,
      rgba(255, 0, 0, 0) 100%,
      rgb(255, 255, 255) 20%
    ),
    url("https://user-images.githubusercontent.com/59159608/161017631-38c370fa-3fee-417f-bde4-3ad9524630e7.jpg");
  filter: blur(8px);
  -webkit-filter: blur(8px);
  height: 100%;
  width: 100%;
  z-index: -1;
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}

.user-signup {
  display: flex;
  flex-direction: column;
  align-content: center;
  margin: 0 auto;
  align-items: center;
  width: 100%;
  height: 100%;
  padding-top: 10% 0em 0em 10%;
  border: 1px solid silver;
  width: 60%;
  border-radius: 5px;
  z-index: 2;
  background-color: white;
}

.user-signup form {
  width: 500px;
}

.user-signup input {
  width: 240px;
  padding: 12px 10px;
  box-sizing: border-box;
}

.user-signup button {
  background-color: #008b9e;
  border: none;
  color: white;
  padding: 16px 32px;
  text-decoration: none;
  margin: 4px 2px;
  cursor: pointer;
}

.user-signup button:hover {
  background-color: #5ab4b4;
}

.user-signup a {
  color: #008b9e;
}

.user-signup a:hover {
  color: #5ab4b4;
}

.user-signup-footer {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  height: 50px;
  border-top: 1px solid silver;
  background-color: white;
  position: fixed;
  bottom: 0px;
}

.user-form-error {
  color: #f42b4b;
  text-align: center;
}
